.play {
  max-width: 300px;
  position: relative;
  margin-top: 130px;
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
}
@media only screen and (max-width: 700px) {
  .triangle{
    width: 200px;
  }
  .icon{
    height: 100px;
    width: 100px;
    border: 1rem solid;
    border-radius: 50%;
  }
  .icon--paper{
    background-size: 10px;
  }
  .icon--paper {
    background-color: #fff;
    background-image: url(/rock-paper-scissors/static/media/icon-paper.8b57a6b1.svg);
    background-size: 40px;
    background-position: center;
    background-repeat: no-repeat;
    border-color: hsl(230, 89%, 62%);
    transform: translateY(-3rem) translateX(2.5rem) ;
  }
  .icon--rock {
    background-color: #fff;
    background-image: url(/rock-paper-scissors/static/media/icon-rock.476e90a9.svg);
    background-size: 40px;
    background-position: center;
    background-repeat: no-repeat;
    border-color: hsl(349, 71%, 52%);
   transform: translateY(-3rem) translateX(-2.5rem);
  }
  .icon--scissors {
    background-color: #fff;
    background-image: url(/rock-paper-scissors/static/media/icon-scissors.3b1a5d7e.svg);
    background-size: 40px;
    background-position: center;
    background-repeat: no-repeat;
    border-color: hsl(39, 89%, 49%);
    transform: translateY(5.5rem);
  }
}
.triangle {
  position: absolute;
  align-self: center;
}
.items {
  display: flex;
  justify-content: center;
}
@media screen and (min-width: 700px){
.icon {
  height: 160px;
  width: 160px;
  border: 1.5rem solid;
  border-radius: 50%;
}
.icon--paper {
  background-color: #fff;
  background-image: url(/rock-paper-scissors/static/media/icon-paper.8b57a6b1.svg);
  background-size: 100px;
  background-position: center;
  background-repeat: no-repeat;
  border-color: hsl(230, 89%, 62%);
  transform: translateY(-6rem) translateX(4rem);
}
.icon--paper:hover {
  transform: translateY(-6rem) translateX(4rem) scale(1.1)
    rotate3d(0, 1, 0, 180deg);
  transition: all 0.8s ease-in-out;
}
.icon--rock {
  background-color: #fff;
  background-image: url(/rock-paper-scissors/static/media/icon-rock.476e90a9.svg);
  background-size: 100px;
  background-position: center;
  background-repeat: no-repeat;
  border-color: hsl(349, 71%, 52%);
 transform: translateY(-6rem) translateX(-4rem) ;
}
.icon--rock:hover {
  transform: translateY(-6rem) translateX(-4rem) scale(1.1)
    rotate3d(0, 1, 0, 180deg);
  transition: all 0.8s ease-in-out;
}
.icon--scissors {
  background-color: #fff;
  background-image: url(/rock-paper-scissors/static/media/icon-scissors.3b1a5d7e.svg);
  background-size: 100px;
  background-position: center;
  background-repeat: no-repeat;
  border-color: hsl(39, 89%, 49%);
  transform: translateY(7rem);
}
.icon--scissors:hover {
  transform: translateY(7rem) scale(1.1)
    rotate3d(0, 1, 0, 180deg);
  transition: all 0.8s ease-in-out;
}
}
.modal-container {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-container .modal-box {
  color: rgb(149, 149, 155);
  background-color: #fff;
  border-radius: 8px;
  padding: 30px;
}
img {
  width: 100%;
}
.modal-container .modal-box .modal__header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}
.modal-container .modal-box .modal__header h1 {
  text-transform: uppercase;
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}
.modal-container .modal-box .modal__header img{
  transform: translateY(-38rem) translateX(-17rem);
}
.modal-container .modal-box .modal__header button {
  background: none;
  border: none;
  align-self: center;
  color: white;
}
.modal-container .modal-box .modal__header button:hover {
  cursor: pointer;
}
button {
  position: absolute;
  bottom: 1rem;
  padding: 7px 25px;
  right: 1rem;
  border-radius: 0.4rem;
  border: 3px solid hsl(217, 16%, 45%);
  color: lighten(hsl(217, 16%, 45%), 20%);
  background: none;
  font-weight: 600;
  letter-spacing: 2px;
}
button:hover {
  background-color: rgba(#ffffff, 0.8);
  color: hsl(229, 25%, 31%);
  cursor: pointer;
}
.attribution{
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  bottom: 1rem;
}
.attribution a{
  text-decoration: none;
  color:white;
}
* {
  text-align: center;
  padding: 0;
  margin: 0;
}

body {
  font-family: "Akaya Telivigala", cursive;
  background: radial-gradient(circle, hsl(214, 47%, 23%), hsl(237, 49%, 15%));
  color: #fff;
}


